.varlist-var-name{
    font-family: 'Mulish',sans-serif;
    color:#6495ED;
    font-weight: bold;
    font-size: 2.5vh;
}
.varbar-header-title{
    font-weight: 350;
    color: #6495ED;  
     
}
.var-list-table>tr{
    height: 3vh;
    min-height: 50px;
    max-height: 100px;
}
.zero-var-wrapper{
    width: 100%;
    height: 100%;
}
.zero-var-tile{
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: 100%;
    height: 80%;
    background-color: whitesmoke;
}